import {Component} from '@angular/core';
import {AlertController, IonicPage, NavController, NavParams} from 'ionic-angular';
import {ConfigProvider} from "../../providers/config/config";
import {CryptoParamUtilProvider} from "../../providers/crypto-param-util/crypto-param-util";
import {MaskTokenProvider} from "../../providers/mask-token/mask-token";
import {LocalStorageProvider} from "../../providers/local-storage/local-storage";
import {TokenServiceProvider} from "../../providers/token-service/token-service";
import {FundTransferOtherBankProvider} from "../../providers/fund-transfer-other-bank/fund-transfer-other-bank";


@IonicPage()
@Component({
  selector: 'page-fund-transer-other-bank03',
  templateUrl: 'fund-transer-other-bank03.html',
})
export class FundTranserOtherBank03Page {

  otherBankTransferError03;
  accountNo;
  fundTransferType;
  receiverType;
  srcAccDetails;
  purposeList;
  selectedDest;
  purposeTitle;
  purposeID;
  formVal = {amount: "", note: ""};
  amount;
  note;
  executionDate;
  routeShow;

  //field for execution date
  minDate;
  maxDate;

  constructor(public navCtrl: NavController,
              public navParams: NavParams,
              public cryptoUtil: CryptoParamUtilProvider,
              public maskTokenUtil: MaskTokenProvider,
              public storage: LocalStorageProvider,
              public ftOtherBankService: FundTransferOtherBankProvider,
              public tokenService: TokenServiceProvider,
              public alertCtrl: AlertController,
              public config: ConfigProvider) {

    this.accountNo = this.navParams.get("accountNo");
    this.fundTransferType = this.navParams.get("fundTransferType");
    this.receiverType = this.navParams.get("receiverType");
    this.srcAccDetails = JSON.parse(this.navParams.get("srcAccDetails"));
    this.purposeList = JSON.parse(this.navParams.get("purposeList"));
    this.selectedDest = JSON.parse(this.navParams.get("selectedDest"));
    this.routeShow = this.fundTransferType != config.transferTypeNPSB;
    console.log(this.fundTransferType);

    this.executionDate=new Date();
    this.minDate=new Date();
    this.maxDate=new Date();
    this.maxDate.setFullYear(this.maxDate.getFullYear()+1);
    this.maxDate.toISOString();
  }

  showPurposePopupList = function () {
    let data = this.purposeList;
    let radioAlert = this.alertCtrl.create();
    radioAlert.setTitle('Select Purpose');
    radioAlert.setCssClass("alertLogCss");

    Object.keys(data).forEach(key => {
      let value = data[key];
      radioAlert.addInput({
        type: 'radio',
        label: value.Title,
        value: value,
        checked: false
      })
    });

    radioAlert.addButton('Cancel');
    radioAlert.addButton({
      text: 'OK',
      handler: data => {
        if (typeof(data) == "undefined") {
        }

        else {

          this.purposeTitle = data.Title;
          this.purposeID = data.ID;
          this.otherBankTransferError03 = "";
        }
      }
    });
    radioAlert.present();
    setTimeout(() => {
      radioAlert.dismiss();
    }, 60000);
  };


  otherBankTransfer03Action = function () {
    this.otherBankTransferError03 = "";
    let validateAmountExp = new RegExp("[0-9]{1,6}$");
    let flag = true;
    this.amount = this.formVal.amount;
    this.note = this.formVal.note;

    if (this.purposeID == "" || typeof(this.purposeID) == "undefined") {
      this.otherBankTransferError03 = "Please Select fund transfer purpose.";
      flag = false;
      return;
    }

    if (this.amount == "" || typeof(this.amount) == "undefined") {
      this.otherBankTransferError03 = "Transfer amount can not be blank.";
      flag = false;
      return;
    }

    if (this.note == "" || typeof(this.note) == "undefined") {
      this.otherBankTransferError03 = "Note is required for transfer.";
      flag = false;
      return;
    }

    if (!validateAmountExp.test(this.amount)) {
      this.otherBankTransferError03 = "Invalid Transfer amount.";
      flag = false;
      this.formVal = {amount: ""};
      return;
    }

    if (this.amount>this.srcAccDetails.availableBalance) {
      this.otherBankTransferError03 = "Transfer amount is greater than available balance.";
      flag = false;
      return;
    }

    if (flag) {
      let appData = this.storage.retrieveFromStorage("appData");
      let unMaskRefToken = this.maskTokenUtil.getUnMaskVal(appData.refreshToken);
      this.tokenService.getNewToken(unMaskRefToken)
        .then(data => {
          this.onNewTokenGenComplete(data);
        });
    }
  };

  onNewTokenGenComplete = function (data) {
    if (data.status == 0) {
      this.navCtrl.setRoot('ExpirePage');
    }
    else {
      let eDay = (this.executionDate.getDate() < 10 ? '0' : '') + this.executionDate.getDate();

      let eMonth=this.executionDate.getMonth()+1;
      eMonth= (eMonth <10 ? '' : '')+eMonth;

      let eDt = eDay.toString() + "/" + eMonth.toString() + "/" + this.executionDate.getFullYear().toString();


      let appData = this.storage.retrieveFromStorage("appData");
      let encRecType = this.cryptoUtil.getEncryptVal(this.receiverType);
      let encTransferType = this.cryptoUtil.getEncryptVal(this.fundTransferType);
      let encSrcAccount = this.cryptoUtil.getEncryptVal(this.accountNo);
      let encAmount = this.cryptoUtil.getEncryptVal(this.amount);
      let encNote = this.cryptoUtil.getEncryptVal(this.note);
      let encPurpose = this.cryptoUtil.getEncryptVal(this.purposeID);
      let encExDate = this.cryptoUtil.getEncryptVal(eDt);
      let encReceiverId = this.cryptoUtil.getEncryptVal(this.selectedDest.beneficiaryId);


      let unMaskToken = this.maskTokenUtil.getUnMaskVal(appData.token);
      this.ftOtherBankService.initOtherBankTransfer(unMaskToken, appData.userId, appData.appId, encRecType, encTransferType, encSrcAccount, encAmount, encNote, encPurpose, encReceiverId, encExDate)
        .then(data => {
          this.intOtherBankFuntransferComplete(data);
        });
    }
  };

  intOtherBankFuntransferComplete = function (data) {
    this.selectedDest = JSON.parse(this.navParams.get("selectedDest"));
    if (data.statusCode == 1) {
      this.navCtrl.push('FundTranserOtherBank04Page', {
        'accountNo': this.accountNo,
        'amount' : this.amount,
        'srcAccDetails': JSON.stringify(this.srcAccDetails),
        'selectedDest': JSON.stringify(this.selectedDest),
        'fundTransferType': this.fundTransferType,
        'receiverType': this.receiverType,
        'initObTxDetails': JSON.stringify(data.object)
      });
    }

    else if (data.statusCode == 0) {
      this.otherBankTransferError03 = data.note;
    }

    else {
      this.otherBankTransferError03 = "System Error Contact with Administrator.";
    }
  };

  ionViewDidLoad() {

  }

}



